.dialogs__name {
    display: block;
    font-size: 20px;
    color: black;
    margin-top: 10px;
    padding-left: 20px;
    position: relative;
}

.dialogs__name::before {
    content: '';
    width: 6px;
    height: 6px;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
    background-color: gray;
    border-radius: 50%;
}