.textarea {
    position: fixed;
    bottom: 0;
    width: 1285px;
    height: 100px;
    outline: none;
    padding: 10px;
}

.textarea__error {
    border-color: red;
}

.error__text {
    color: black;
    background-color: red;
    min-width: 100px;
    height: 35px;
    border-radius: 40px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    padding-left: 15px;
    bottom: 100px;
    transform: translateY(-50%);
}

.block {
    position: relative;
}