.dialogs__names-title {
    font-weight: 350;
}

.dialogs {
    display: grid;
    grid-template-columns: 2fr 10fr;
    grid-auto-rows: 1fr;
}

.messege__wrapper {
    padding-left: 10px;
    padding: 10px 0;
    height: 900px;
    width: 100%;
    position: relative;
    overflow: hidden;
    overflow-y: scroll;
    z-index: 6;
}

.messege__textarea {
    position: fixed;
    width: 100%;
    bottom: 0px;
    height: 100px;
    padding: 10px;
    z-index: 5;
}

.messege__wrapper::before {
    content: '';
    width: 1px;
    height: 900px;
    background-color: black;
    position: fixed;
}

.messege__btn {
    width: 40px;
    height: 40px;
    border: 1px solid #000;
    position: fixed;
    bottom: 30px;
    right: 320px;
    background-color: #13CE66;
    border-radius: 50%;
    z-index: 10;
}

.messege__btn::before {
    content: '>';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    font-weight: 600;
}